.container {
  position: relative;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1064px; }

.header {
  position: relative;
  z-index: 2;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.9); }
  .header .container {
    display: flex;
    justify-content: space-between; }
    .header .container::after {
      display: none; }
    @media (max-width: 890px) {
      .header .container {
        flex-flow: column wrap;
        justify-content: center; } }
  .header__logo {
    display: inline-block;
    margin-top: 31px;
    margin-left: 17px; }
    @media (max-width: 570px) {
      .header__logo {
        margin-bottom: 11px;
        text-align: center; } }
  .header__menu {
    display: flex;
    list-style-type: none;
    margin: 0 0 0 83px;
    padding: 0; }
    .header__menu__item {
      position: relative;
      padding: 41px 0 27px; }
      .header__menu__item__link {
        padding: 41px 16px 27px;
        font-size: 18px;
        line-height: 18px;
        text-decoration: none;
        color: #626262; }
        @media (max-width: 450px) {
          .header__menu__item__link {
            padding: 12px 16px 6px; } }
      .header__menu__item__submenu {
        position: absolute;
        left: 0;
        top: 86px;
        z-index: 5;
        display: flex;
        overflow: hidden;
        height: 0;
        background: #cf1a2b;
        opacity: 0;
        transition: opacity 0.1s ease; }
        .header__menu__item__submenu__col-1 {
          flex-grow: 1; }
          .header__menu__item__submenu__col-1 .logo {
            display: block;
            border: solid 1px rgba(255, 255, 255, 0.2);
            padding: 23px 50px 18px 35px; }
          .header__menu__item__submenu__col-1 .tie {
            display: block;
            border-right: solid 1px rgba(255, 255, 255, 0.2);
            padding: 53px 62px 53px 73px; }
          @media (max-width: 1100px) {
            .header__menu__item__submenu__col-1 {
              display: none; } }
        .header__menu__item__submenu__col-2 {
          display: flex;
          flex-grow: 1;
          flex-direction: column;
          justify-content: center;
          border-right: solid 1px rgba(255, 255, 255, 0.2);
          padding: 0 36px; }
        .header__menu__item__submenu__col-3 {
          display: flex;
          flex-grow: 1;
          flex-direction: column;
          justify-content: center;
          padding: 0 36px; }
        .header__menu__item__submenu__link {
          margin: 1px 0;
